<template>
    <div>
        <AdminNavbar/>
        <Alert></Alert>
        <div class="container-fluid">
            <div class="row">
                <Sidebar></Sidebar>
                <main role="main" class="col-md-9 ml-sm-auto col-lg-10 px-4">
                    <router-view></router-view>
                </main>
                
            </div>
        </div>
        
    </div>
</template>
<script>
    import Sidebar from './Sidebar';
    import AdminNavbar from './AdminNavbar';
    import Alert from './Alert';


    export default {
    components: {
        Sidebar,
        AdminNavbar,
        Alert,
      },
    };
</script>


